/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -*/
/* General */

* {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  height: 100%;
  background-color: var(--background-color-html);
}

*, *:before, *:after {
  box-sizing: inherit;
}

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -*/
/* Body */

body {
  overflow-y: scroll;

  font-size: 16px;
  font-family: var(--font-main);
  height: 100%;

  background-color: var(--background-color-body);
  
  -webkit-overflow-scrolling: touch;
}

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -*/
/* Images */

img {
  max-width: 100%;
}

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -*/
/* Headers */

h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-heading);
}

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -*/
/* Anchors */

a {
  text-decoration: none;
  color: var(--color-font);
}

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -*/
/* Buttons */

button {
  color: var(--theme__button__color);
  border: var(--theme__button__border);
  background-color: var(--theme__button__background-color);
  padding: var(--theme__button__padding);
  border-radius: var(--theme__button__border-radius);
  transition: var(--theme__button__transition);
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.241);
  margin: 2px 2px 2px 2px;
}

button:hover {
  background-color: var(--theme__button--hover__background-color);
}

button:active {
  background-color: var(--theme__button--active__background-color);
}

button:focus {
  /* Hide blue border when pressing button in Google Chrome. */
  outline: var(--theme__button--focus__outline);
}

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -*/
/* Tables */

table {
  width: 100%;
}

.table-wrapper {
  overflow: auto;
}

thead {
  background: var(--color-font);
  color: var(--color-background);
}

td,
th {
  padding: 5px;
  text-align: left;
}

tbody tr:nth-child(2n) {
  background: var(--color-gray);
  -webkit-appearance: none;
}

tfoot {
}

/* -------------------------------------------------------------------------- */

textarea {
  border-radius: 1px;
  border-style:solid;
  -webkit-appearance: none;
  font-size: 1rem;
}

textarea:focus {
  outline: 0;
}



/* -------------------------------------------------------------------------- */

/* ... */

#root {
  height: 100%;
}
