:root {
  /* ------------------------------------------------------------------------ */
  /* Define global variables */

  /* TODO: bad naming convention! */

  --transition: 250ms ease-in-out;

  /* ------------------------------------------------------------------------ */
  /* Color schemes */

  --scheme-1__color-1: #eae7dc;
  --scheme-1__color-2: #d8c3a5;
  --scheme-1__color-3: #8e8d8a;
  --scheme-1__color-4: #e98074;
  --scheme-1__color-5: #e85a4f;

  --scheme-2__color-1: #e27d60;
  --scheme-2__color-2: #85dcb0;
  --scheme-2__color-3: #e8a87c;
  --scheme-2__color-4: #c38d9e;
  --scheme-2__color-5: #41b3a3;

  /* ------------------------------------------------------------------------ */
  /* Theme 0 */
  
  --theme-0-background-color-top-bar: #e8e8e8;
  --theme-0-border-bottom-top-bar: 1px solid #d6d6d6;

  --theme-0-background-color-content: #e8e8e8;
  
  --theme-0-background-color-bottom-bar: #e8e8e8;
  --theme-0-border-top-bottom-bar: 1px solid #d6d6d6;


  --theme-0__button__border: 1px solid rgb(160, 160, 160);
  --theme-0__button__background-color: rgb(226, 226, 226);
  --theme-0__button__padding: 8px 8px;
  --theme-0__button__border-radius: 4px;
  --theme-0__button__transition: background-color var(--transition);
  
  --theme-0__button--hover__background-color: rgb(199, 199, 199);
  --theme-0__button--active__background-color: rgb(179, 179, 179);
  --theme-0__button--focus__outline: 0;

  /* ------------------------------------------------------------------------ */
  /* Theme 1 */
  
  --theme-1-background-color-top-bar: rgb(255, 238, 213);
  --theme-1-border-bottom-top-bar: 1px solid rgb(255, 214, 157);

  --theme-1-background-color-content: rgb(255, 249, 240);

  --theme-1-background-color-bottom-bar: rgb(255, 233, 202);
  --theme-1-border-top-bottom-bar: 1px solid rgb(255, 214, 157);

  --theme-1__button__border: 1px solid rgb(160, 160, 160);
  --theme-1__button__background-color: rgb(226, 226, 226);
  --theme-1__button__padding: 8px 8px;
  --theme-1__button__border-radius: 2px;
  --theme-1__button__transition: background-color var(--transition);
  
  --theme-1__button--hover__background-color: rgb(199, 199, 199);
  --theme-1__button--active__background-color: rgb(179, 179, 179);
  --theme-1__button--focus__outline: 0;

  /* ------------------------------------------------------------------------ */
  /* Theme 2 */


  --theme-2__top-bar__background-color: rgb(231, 231, 231);
  --theme-2__top-bar__border-bottom: 1px solid rgb(214, 214, 214);

  --theme-2__content__background-color: rgb(253, 253, 253);

  --theme-2__bottom-bar__background-color: rgb(194, 194, 194);
  --theme-2__bottom-bar__border-top: 1px solid rgb(173, 173, 173);

  --theme-2__button__color: rgb(39, 39, 39);
  --theme-2__button__border: 1px solid rgb(202, 202, 202);
  --theme-2__button__background-color: rgb(221, 221, 221);
  --theme-2__button__padding: 8px 8px;
  --theme-2__button__border-radius: 1px;
  --theme-2__button__transition: background-color var(--transition);
  
  --theme-2__button--hover__background-color: rgb(199, 199, 199);
  --theme-2__button--active__background-color: rgb(179, 179, 179);
  --theme-2__button--focus__outline: 0;

  /* ------------------------------------------------------------------------ */
  /* Selected theme */

  --theme__top-bar__background-color: var(--theme-2__top-bar__background-color);
  --theme__top-bar__border-bottom: var(--theme-2__top-bar__border-bottom);

  --theme__content__background-color: var(--theme-2__content__background-color);

  --theme__bottom-bar__background-color: var(--theme-2__bottom-bar__background-color);
  --theme__bottom-bar__border-top: var(--theme-2__bottom-bar__border-top);

  --theme__button__color: var(--theme-2__button__color);
  --theme__button__border: var(--theme-2__button__border);
  --theme__button__background-color: var(--theme-2__button__background-color);
  --theme__button__padding: var(--theme-2__button__padding);
  --theme__button__border-radius: var(--theme-2__button__border-radius);
  --theme__button__transition: var(--theme-2__button__transition);

  --theme__button--hover__background-color: var(--theme-2__button--hover__background-color);
  --theme__button--active__background-color: var(--theme-2__button--active__background-color);
  --theme__button--focus__outline: var(--theme-2__button--focus__outline);


  --theme__task-edit-entry__margin-top: 12px;
  --theme__task-edit-entry__margin-bottom: 12px;
  --theme__task-edit-entry__padding-bottom: 4px;
  --theme__task-edit-entry__padding-left: 1px;
  --theme__task-edit-entry__padding-right: 1px;
  --theme__task-edit-entry__border: 1px solid rgb(230, 230, 230);
  --theme__task-edit-entry__box-shadow: 0px 0px 6px rgba(0,0,0,0.4);

  --theme__task-edit-entry-header__padding-top: 4px;
  --theme__task-edit-entry-header__padding-bottom: 4px;
  --theme__task-edit-entry-header__margin-bottom: 4px;
  --theme__task-edit-entry-header__background-color: rgb(245, 245, 245);
  --theme__task-edit-entry-header__box-shadow: 0px 0px 4px rgba(0,0,0,0.2);


  /* ------------------------------------------------------------------------ */

  
  --color-black: #000000;
  --color-font: #222;
  --color-background: #eee;
  --color-gray: #ccc;
  --color-invalid: #4e1d04;
  

  --color-missing: rgb(255, 9, 132);
  --missing: var(--color-missing);

  --background-color-html: var(--theme-background-color-content);
  --background-color-body: var(--theme-background-color-content);



  --main-bg-color: brown;
  
  --max-width: 1400px;
  --gutter: 30px;

  --font-main: 'Open Sans', helvetica, arial, sans-serif;

  --font-heading: 'Noto Serif', georgia, serif;

  /* ------------------------------------------------------------------------ */
}
