.topBar {
  border-bottom: var(--theme__top-bar__border-bottom);
  background-color: var(--theme__top-bar__background-color);

  box-shadow: 0 8px 8px -8px rgba(0, 0, 0, 0.25);
}

.content {
  background-color: var(--theme__content__background-color);
}

.bottomBar {
  border-top: var(--theme__bottom-bar__border-top);
  background-color: var(--theme__bottom-bar__background-color);
  box-shadow: 0 -8px 8px -8px rgba(0, 0, 0, 0.25);
}
